<template>
	<div class="loginbox">
		<div class="leftBg"></div><div class="rightBg"></div>
		<div class="login_main">
			<div class="loginform">
				<div class="login-left">
				<h3><span>登录</span></h3>
					<form ref='login'>
						<div class="form_item">
							<ul>
							   <li>
							   		<Icon type="person" class="itemicon"></Icon><Input placeholder="请输入登录账号" clearable class="forminput" v-model="inputval.email"></Input>
							   </li>
							   <li>
									<Icon type="ios-unlocked" class="itemicon"></Icon></Icon><Input placeholder="请输入登录密码" type="password" clearable class="forminput" v-model="inputval.password"></Input>
							   </li>
							   <li class="clearfix">
							    	<span style="float:left">
								    	<Checkbox label="twitter">
								            <span>记住密码</span>
								        </Checkbox>
							        </span>
							    	<span style="float:right"><router-link to="/findpassword">忘记密码</router-link></span>
							   </li>
							   <li>
									<Button type="primary" :loading="loading" @click="loginaction">
								        <span v-if="!loading">登录</span>
								        <span v-else>登录中...</span>
								    </Button>
							   </li>
							</ul>
						</div>
					</form>
					<div class="form_item other-login">
						<ul>
						   <li>
							    <Row type="flex" justify="center" class="code-row-bg">
							        <Col span="4"><a title="微信登录"><i class="iconfont iconweixin icon-weixin1"></i></a></Col>
							        <Col span="4"><a title="QQ登录"><i class="iconfont iconqq icon-qq1"></i></a></Col>
							        <Col span="4"><a title="微博登录"><i class="iconfont iconweibo icon-weibo1"></i></a></Col>
							        <Col span="4"><a title="GitHub登录"><i class="iconfont iconGitHub icon-GitHub"></i></a></Col>
							        <Col span="4"><a title="CSDN登录"><i class="iconfont iconcsdn icon-csdn"></i></a></Col>
							    </Row>
						   </li>
						   <li>还没有账号？入群获取【203849104】</li>
						</ul>
					</div>
				</div>
				<div class="login-right">

				</div>
			</div>
		</div>
		<div></div>
	</div>
</template>

<script>
import cookies from 'js-cookie'
export default {
  data () {
    return {
      loading: false,
      inputval: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    loginaction () {
      if (this.inputval.email !== 'jumao' || this.inputval.password !== 'jumao1234') {
        this.$Message.error('登录账号或者密码不正確')
      } else {
        var redirect = this.$route.query.redirect
        if (typeof (redirect) !== 'string') {
          redirect = '/'
        }
        console.log(redirect)
        cookies.set('user', this.email)
        this.$router.push({ path: redirect })
      }
    }
  }
}
</script>

<style>
html, body {
    width: 100%;
    height: 100%;
    background: #f0f0f0;
    overflow: hidden;
    font-weight:300;
}
</style>


